import React, { useState, useEffect } from 'react'
import './index.scss'
import guitarImg from '../../assets/images/guitars.webp'

import nftWebm from '../../assets/videos/nft.webm'
import nftMain from '../../assets/videos/nft.mp4'
import Header from '../../components/header/Header'

import { useDispatch, useSelector } from "react-redux";
import { fetchData } from "../../redux/data/dataActions";
import { toast } from 'react-toastify';

import whitelistedAddresses from '../../addresses.json'
import MerkleTree from 'merkletreejs'
import keccak256 from 'keccak256'

import loading from '../../assets/images/loading.svg'
import Timer from '../../misc/timer'
import WalletModal from '../../components/wallet-modal/WalletModal';

function index() {
    const [walletOpen, setWalletOpen] = useState(false);

    const blockchain = useSelector((state) => state.blockchain);

    const [claimingNft, setClaimingNft] = useState(false);
    const [limit, setLimit] = useState(null)
    const [mintAmount, setMintAmount] = useState(1)
    const [maxPerWallet, setMaxPerWallet] = useState(null);

    const [refresh, setRefresh] = useState(0);
    const [dataLoaded, setDataLoaded] = useState(false)
    const [addressWhitelisted, setAddressWhitelisted] = useState(false)
    const [cost, setCost] = useState(null)

    const [presaleActive, setPresaleActive] = useState(null)
    const [publicActive, setPublicActive] = useState(null)
    const [timer, setTimer] = useState(null)

    const [CONFIG, SET_CONFIG] = useState({
        CONTRACT_ADDRESS: "",
        NETWORK: {
            NAME: "",
            SYMBOL: "",
            ID: 0,
        },
        GAS_LIMIT: 0,
    });
    const getConfig = async () => {
        const configResponse = await fetch("/config/config.json", {
            headers: {
                "Content-Type": "application/json",
                Accept: "application/json",
            },
        });
        const config = await configResponse.json();
        SET_CONFIG(config);
    };

    useEffect(() => {
        getConfig();

    }, []);

    const presaleMint = () => {
        if (claimingNft) return

        let gasLimit = CONFIG.GAS_LIMIT;
        let totalCostWei = String(cost * mintAmount);
        let totalGasLimit = String(gasLimit * (mintAmount > 2 ? mintAmount - (mintAmount / 2) : mintAmount));


        const whiteListAddressesLeaves = whitelistedAddresses.map((x) => keccak256(x))
        const merkletree = new MerkleTree(whiteListAddressesLeaves, keccak256, { sortPairs: true })
        const merklePoof = merkletree.getHexProof(keccak256(blockchain.account))

        toast.info(`Minting your ${mintAmount > 1 ? 'Rock Party NFTs' : 'Rock Party NFT'}...`);
        setClaimingNft(true);


        blockchain.smartContract.methods.presaleMint(merklePoof, mintAmount)
            .send({
                gasLimit: String(totalGasLimit),
                to: CONFIG.CONTRACT_ADDRESS,
                from: blockchain.account,
                value: totalCostWei,
            })
            .once("error", (err) => {
                console.log(err);
                toast.error("Sorry, something went wrong please try again later.");
                setClaimingNft(false);
            })
            .then((receipt) => {
                console.log(receipt);
                toast.success(() =>
                    <span>
                        Rock Party NFT Mint Successful! Visit <a href="https://opensea.io/collection/rock-party-classic-collection">opensea.io</a> to view your NFT.
                    </span>
                );
                setClaimingNft(false);
                setRefresh(refresh + 1)
                setMintAmount(1)
            });
    };

    const publicMint = () => {
        if (claimingNft) return

        let gasLimit = CONFIG.GAS_LIMIT;
        let totalCostWei = String(cost * mintAmount);
        let totalGasLimit = String(gasLimit * (mintAmount > 2 ? mintAmount - (mintAmount / 2) : mintAmount));

        setClaimingNft(true);
        toast.info(`Minting your ${mintAmount > 1 ? 'Rock Party NFTs' : 'Rock Party NFT'}...`);

        blockchain.smartContract.methods
            .publicMint(mintAmount)
            .send({
                gasLimit: String(totalGasLimit),
                to: CONFIG.CONTRACT_ADDRESS,
                from: blockchain.account,
                value: totalCostWei,
            })
            .once("error", (err) => {
                console.log(err);
                toast.error("Sorry, something went wrong please try again later.");
                setClaimingNft(false);
            })
            .then((receipt) => {
                console.log(receipt);
                toast.success(() =>
                    <span>
                        Rock Party NFT Mint Successful! Visit <a href="https://opensea.io/collection/rock-party-classic-collection">opensea.io</a> to view your NFT.
                    </span>
                );
                setClaimingNft(false);
                setRefresh(refresh + 1)
                setMintAmount(1)
            });
    };

    const decrementMintAmount = () => {
        let newMintAmount = mintAmount - 1;
        if (newMintAmount < 1) {
            newMintAmount = 1;
        }
        setMintAmount(newMintAmount);
    };

    const incrementMintAmount = () => {
        if (limit === null) return
        let newMintAmount = mintAmount + 1;
        if (newMintAmount > limit) {
            newMintAmount = limit;
        }
        if (limit === 0) {
            newMintAmount = 1
        }
        setMintAmount(newMintAmount);
    };


    const initConnect = async (connectedAccount) => {
        // console.log(connectedAccount)

        try {
            setAddressWhitelisted(isWhitelisted(connectedAccount))

            const presaleCost = await blockchain.smartContract.methods.presaleCost().call();
            const publicSaleCost = await blockchain.smartContract.methods.publicSaleCost().call();

            const presaleActive = await blockchain.smartContract.methods.presaleActive().call();
            const publicActive = await blockchain.smartContract.methods.publicSaleActive().call();

            const maxMintAmountPerPresaleAccount = await blockchain.smartContract.methods.maxMintAmountPerPresaleAccount().call();
            const maxMintAmountPerPublicAccount = await blockchain.smartContract.methods.maxMintAmountPerPublicAccount().call();


            if (presaleActive) {
                console.log('working')
                setMaxPerWallet(maxMintAmountPerPresaleAccount)
                setCost(presaleCost)
                setPresaleActive(true)
                setPublicActive(false)

                await blockchain.smartContract.methods.numberMinted(blockchain.account).call().then((result) => {
                    let limit = Number(maxMintAmountPerPresaleAccount) - result;

                    if (limit <= 0) {
                        setLimit(0)
                    }
                    else {
                        setLimit(limit)
                    }
                })

            }

            else if (publicActive) {
                setMaxPerWallet(maxMintAmountPerPublicAccount)
                setCost(publicSaleCost)
                setPublicActive(true)
                setPresaleActive(false)

                await blockchain.smartContract.methods.getPublicAmountMinted(blockchain.account).call().then((result) => {
                    let limit = Number(maxMintAmountPerPublicAccount) - result;

                    if (limit <= 0) {
                        setLimit(0)
                    }
                    else {
                        setLimit(limit)
                    }
                })
            }

            setDataLoaded(true)
        }
        catch (err) {
            console.log(err)
        }

    }

    useEffect(() => {
        if (blockchain.smartContract === null) return;

        initConnect(blockchain.account)

        if (timer !== null) {
            timer.stop()
        }

        let timer2 = new Timer(() => {
            initConnect(blockchain.account)
        }, [10000]);

        setTimer(timer2)
    }, [blockchain.account, blockchain.smartContract, refresh])

    // useEffect(() => {
    //   if (blockchain.account === null || dataLoaded === false) return;

    //   if (presaleActive === true) {
    //     blockchain.smartContract.methods.numberMinted(blockchain.account).call().then((result) => {
    //       let limit = Number(maxPerWallet) - result;
    //       // console.log(typeof(maxPerWallet))
    //       console.log(limit)
    //       if (limit <= 0) {
    //         setLimit(0)
    //       }
    //       else {
    //         setLimit(limit)
    //       }
    //     })
    //   }
    //   else if (publicActive === true) {
    //     blockchain.smartContract.methods.getPublicAmountMinted(blockchain.account).call().then((result) => {
    //       let limit = Number(maxPerWallet) - result;

    //       if (limit <= 0) {
    //         setLimit(0)
    //       }
    //       else {
    //         setLimit(limit)
    //       }
    //     })
    //   }

    // }, [refresh, blockchain.account, dataLoaded, presaleActive, publicActive, maxPerWallet])

    const isWhitelisted = (account) => {
        const whiteListAddressesLeaves = whitelistedAddresses.map((x) => keccak256(x));
        const merkletree = new MerkleTree(whiteListAddressesLeaves, keccak256, { sortPairs: true })
        const merklePoof = merkletree.getHexProof(keccak256(account));
        const rootNode = merkletree.getRoot().toString('hex');
        let whitelisted = merkletree.verify(merklePoof, keccak256(account), rootNode)
        // console.log(whitelisted)
        return whitelisted
    }


    console.log(dataLoaded, limit)

    return (
        <div className='mint-page'>
            <Header />
            <WalletModal open={walletOpen} setOpen={setWalletOpen}></WalletModal>

            <div className='mint-page-bg'>
                <img src={guitarImg} />
            </div>
            <div className='mint-page-inner'>
                <div className='mint-page-nft-video'>
                    <video playsInline loop muted autoPlay>
                        <source src={nftWebm}></source>
                        <source src={nftMain}></source>
                    </video>
                </div>
                <div className='mint-page-mint-wrapper'>
                    <div className='mint-page-mint-amount-wrapper '>
                        <button onClick={() => { decrementMintAmount() }} className='mint-page-mint-amount-btn ffam-lato fs-ms tc-white fw-bold'>-</button>
                        <span className='ffam-lato fs-m tc-white fw-bold'>{mintAmount}</span>
                        <button onClick={() => { incrementMintAmount() }} className='mint-page-mint-amount-btn  ffam-lato fs-ms tc-white fw-bold'>+</button>
                    </div>
                    {
                        blockchain.account === null &&
                        <button onClick={() => { setWalletOpen(true) }} className='mint-page-mint-btn'>
                            <span className='ffam-lato fs-ms tc-white fw-black'>
                                Connect Wallet
                            </span>
                        </button>
                    }
                    {
                        (blockchain.smartContract !== null && dataLoaded === false) && limit === null &&
                        <div className='mint-page-mint-btn loading-button'>
                            <img src={loading} />
                        </div>
                    }
                    {
                        blockchain.smartContract !== null && (presaleActive === null && publicActive === null) && dataLoaded === true &&
                        <div className='mint-page-mint-btn mint-loading-button'>
                            <span className='ffam-lato fs-ms tc-white fw-black'>
                                Mint Coming Soon
                            </span>
                        </div>
                    }
                    {
                        blockchain.smartContract !== null && presaleActive === true && addressWhitelisted === true && limit > 0 &&
                        <button onClick={() => presaleMint()} className='mint-page-mint-btn'>
                            {
                                claimingNft ?
                                    <img src={loading} />
                                    :
                                    <span className='ffam-lato fs-ms tc-white fw-black'>
                                        MINT
                                    </span>
                            }

                        </button>
                    }
                    {
                        blockchain.smartContract !== null && presaleActive === true && addressWhitelisted === false && dataLoaded === true &&
                        <div className='mint-page-mint-btn loading-button'>
                            <span className='ffam-lato fs-ms tc-white fw-black'>
                                Address Not Whitelisted
                            </span>
                        </div>
                    }
                    {
                        blockchain.smartContract !== null && publicActive === true && limit > 0 &&
                        <button onClick={() => publicMint()} className='mint-page-mint-btn'>
                            {
                                claimingNft ?
                                    <img src={loading} />
                                    :
                                    <span className='ffam-lato fs-ms tc-white fw-black'>
                                        MINT
                                    </span>
                            }

                        </button>
                    }
                    {
                        blockchain.smartContract !== null && limit === 0 &&
                        <div className='mint-page-mint-btn loading-button'>
                            <span className='ffam-lato fs-ms tc-white fw-black'>
                                Mint Limit Exceeded
                            </span>
                        </div>
                    }
                </div>
            </div>
        </div>
    )
}

export default index