import React, { useState, useEffect } from 'react'
import WalletModal from '../../components/wallet-modal/WalletModal'
import { useSelector } from "react-redux";
import mainVideo from '../../assets/videos/backstage.mp4'
import videoWebm from '../../assets/videos/backstage.webm'

import './index.scss'
import Header from '../../components/header/Header'
import Timer from '../../misc/timer'

function index() {
    const blockchain = useSelector((state) => state.blockchain);
    const [balance, setBalance] = useState(null)
    const [timer, setTimer] = useState(null)
    const [modalOpen, setModalOpen] = useState(blockchain.account === null);

    const initConnect = async (connectedAccount) => {

        try {
            let balance = blockchain.smartContract.methods.balanceOf(blockchain.account).call().then((result) => {
                setBalance(Number(result))
            })

        }
        catch (err) {
            console.log(err)
        }

    }

    useEffect(() => {
        if (blockchain.smartContract === null) return;

        initConnect(blockchain.account)

        if (timer !== null) {
            timer.stop()
        }

        let timer2 = new Timer(() => {
            initConnect(blockchain.account)
        }, [10000]);

        setTimer(timer2)
    }, [blockchain.account, blockchain.smartContract])

    return (
        <div className='backstage-page'>

            <WalletModal open={modalOpen} setOpen={setModalOpen} />


            {blockchain.account !== null && balance > 0 &&
                <div>
                    <Header />

                    <div className='backstage-video-wrapper'>
                        <video className='top-section-video' autoPlay playsInline loop muted>
                            <source src={videoWebm} />
                            <source src={mainVideo} />
                        </video>
                    </div>
                </div>
            }


            {balance === 0 &&
                <div>
                    <Header />
                    <div className='backstage-page-inner'>

                        <span className='tc-white ffam-lato fs-s'>You do not own a Rock Party Classic Collection NFT.  Purchase one to obtain access to the Backstage.</span>

                    </div>
                </div>
            }
        </div>
    )
}

export default index